ul li {
  list-style-type: georgian;
}
.app-wrapper {
  margin: auto;
  display: grid;
  width: 1100px;
  height: 100%;
  grid-template-areas: 
  'h h'
  'n c';

  grid-template-rows: auto 1fr;
  grid-template-columns: 2fr 8fr;
  
}
.app-wrapper-content {
  grid-area: c;
  background-color: #faf2f2;
  padding: 10px;
}

